__.mapLegend = function () {

    if ( !$('.map-legend').length ) $(`#map .leaflet-bottom.leaflet-right`).append('<div class="map-legend"></div>');


    this.default = function () {
        $('.map-legend').html(`
        
            ${legendItem(', , ','#9c27b0')}

            ${legendItem('','#0464ff')}

        `);

        return false
    };

    this.eco = function () {

        $('.map-legend').html(`

            ${legendItem('','rgba(0, 98, 255,1)')}

            ${legendItem('   ','rgba(255, 30, 0,1)')}
        
        `);

        return false;
    }

    this.river = function () {

        $('.map-legend').html(`

            ${legendItem('    ','rgba(3, 97, 251,1)')}

            ${legendItem('','rgba(255, 0, 0,1)')}
        
        `);

        return false;
    }

    this.cost = function () {

        $('.map-legend').html(`

            ${legendItem('< 2 . .','rgba(0, 255, 209, 1)')}

            ${legendItem('< 5 . .','rgba(73, 255, 0, 1)')}

            ${legendItem('< 8 . .','rgba(203, 255, 0, 1)')}

            ${legendItem('< 10 . ','rgba(255, 215, 0, 1)')}

            ${legendItem('','rgba(225, 0, 0, 1)')}

        `);

        return false;
    }

    this.forest = function () {

        $('.map-legend').html(`
        
            ${legendItem(' ','rgba(100, 250, 30, 1)')}

            ${legendItem('1.5   ','rgba(250, 230, 30, 1)')}

            ${legendItem(' ','rgba(225, 0, 0, 1)')}

        `);

        return false;
    }

    this.railroad = function () {

        $('.map-legend').html(`

            ${legendItem(' ','rgba(0, 98, 255, 1)')}

            ${legendItem(' ','rgba(226, 30, 220, 1)')}

        `);

        return false;
    }

    this.car = function () {

        $('.map-legend').html(`

            ${legendItem('  30 ','rgba(0, 177, 255, 1)')}

            ${legendItem('   ','rgba(226, 30, 100, 1)')}

        `);

        return false;
    }

    this.markets = function () {

        $('.map-legend').html(`

            ${legendItem('','rgba(0, 177, 255, 1)')}

            ${legendItem('','rgba(226, 30, 100, 1)')}

        `);

        return false;
    }

    this.school = function () {

        $('.map-legend').html(`

            ${legendItem(' ','rgba(100, 250, 30, 1)')}

            ${legendItem('','rgba(226, 30, 100, 1)')}

        `);

        return false;
    }

    this.water = function () {

        $('.map-legend').html(`
        
            ${legendItem(' < 20 ','rgba(40, 130, 255, 1)')}

            ${legendItem(' > 200 ','rgba(20, 20, 137, 1)')}

        `);

        return false;
    }

    function legendItem(title,color){
        return `
        <div class="item">
            <div class="item__point" style="background:${color}"></div>
            <div class="item__description">${title}</div>
        </div>`
    }

    return this;

}    